@use 'src/shared/styles/utils' as *;

.likeButton {
    width: 30px;
    height: 28px;
    background-color: $color-gray-light;
    border: 0px;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}




.icon {
    fill: $color-gray-light;

    path {
        stroke: $color-black;
    }
}

.isActive {
    .icon {
        fill: $color-primary;

        path {
            stroke: $color-primary;
            stroke-width: 0px;
        }
    }
}

.likeButton:hover {
    .icon {
        path {
            stroke: $color-primary;
            stroke-width: 1px;
        }
    }
}

.likeButton2 {
    width: 30px;
    height: 28px;

    background-image: url(../../assets/images/like.svg);
    background-size: 14px;
    background-repeat: no-repeat;
    background-position: center;

    background-color: $color-gray-light;

    border: 0px;
    border-radius: 4px;

    img {
        fill: green;
    }

}