.page {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-block: 20px;
    background-color: var(--color-red);
    row-gap: 24px;
    padding: 16px;
    border-radius: 16px;

    background-color: var(--color-white);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.list-input {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    align-items: flex-start;
}

.title-form {
    font-size: 20px;
    line-height: 24px;
}